<template>
  <div class="column paddingTop">
    
    <div class="column__list">
      <uc-cell
        v-for="(item) of column"
        :key="item._id"
        :data="item"
        :to="`/detail/${item._id}?collectionName=column`"
      />
    </div>
    
    
    
  </div>
</template>

<script>
  import UcCell from '../components/uc-cell/uc-cell.vue'
  import {mapState} from 'vuex'
  
  export default {
	computed:mapState('column',['column']),
    components:{
      UcCell
    },
    
    mounted(){
       this.$store.dispatch('column/column')
    }
  }
</script>

<style scoped>
  .column__list{
    padding: 0 .2rem;
  }
</style>
